import m from 'mithril'
import AppState from './appstate'
import K from './k'
import MCQList from './mcq-list'
import Message from './message'

class Home {
  oninit() {
    this.chips = [
      'Medicine',
      'Surgery',
      'Obstetrics',
      'Gynecology',
      'Pediatrics',
    ]
    this.keyword = this.chips[0]
    this.results = []
    this.error = null
    this.state = AppState.INIT
  }
  oncreate() {
    if (!this.results.length) {
      this.search(this.keyword)
    }
  }
  search(query) {
    if (!query.length) {
      return
    }
    if (this.state == AppState.LOADING) return
    this.state = AppState.LOADING
    m.redraw()
    m.request(
      'https://evokeapps.000webhostapp.com/gmp/rest.php/search/' +
        encodeURIComponent(this.keyword),
    )
      .then((res) => {
        if (res.error != null) {
          throw Error(res.error)
        }
        this.results = res.payload.map((item) => {
          return {
            stem: item['stem'],
            _id: item['_id'],
            options: [item['opt1'], item['opt2'], item['opt3'], item['opt4']],
          }
        })
        this.state = AppState.READY
      })
      .catch((error) => {
        this.error = error
        this.state = AppState.ERROR
      })
      .finally(() => {
        m.redraw()
      })
  }
  view(vnode) {
    return [
      m(
        'nav.navbar.navbar-dark',
        {
          style: {
            'background-color': K.primaryColor,
          },
        },
        m('.container', [m('a.navbar-brand.fw-bold', 'Gulf MCQ Pro')]),
      ),
      m('.container', [
        m(
          '.row.py-3.border-bottom.mb-3',
          m(
            '.col',
            this.chips.map((item) => {
              return m(
                'span.badge.rounded-5.m-1.fs-6.text-white',
                {
                  style: {
                    'background-color':
                      item == this.keyword ? K.primaryColor : '#afafaf',
                  },
                  onclick: (_) => {
                    this.keyword = item
                    this.search(this.keyword)
                  },
                },
                item,
              )
            }),
          ),
        ),
        this.state == AppState.INIT
          ? m(Message, { message: 'Start by searching…' })
          : null,
        this.state == AppState.READY
          ? this.results.length
            ? m(MCQList, { items: this.results })
            : m(Message, { message: 'No results' })
          : null,
        this.state == AppState.LOADING
          ? m(Message, { message: 'Loading…' })
          : null,
        this.state == AppState.ERROR
          ? m(Message, { message: this.error })
          : null,
      ]),
    ]
  }
}

export default Home
