import m from 'mithril'

class MCQList {
  view(vnode) {
    return m(
      'div',
      vnode.attrs.items.map((item, index) => {
        return m(
          '.shadow-sm.border.rounded.p-3.h-100.mb-3',
          {
            style: {
              'border-color': '#fafafa',
            },
          },

          m(
            '.mb-3.fs-5',
            {
              style: {
                'white-space': 'pre-wrap',
              },
            },
            `${index + 1}) ${item['stem']}`,
          ),
          item.options.map((option, i) => {
            return m('.fs-6.text-muted.mb-1', `${i + 1}. ${option}`)
          }),
          m('span.text-muted.mt-4', 'Discussion'),
        )
      }),
    )
  }
}

export default MCQList
